<template>
  <div>
    <b-list-group-item button
      @click="emitClick"
      :id="`${showImgButtonclass}+${showImgButtonId}`"
    >{{showImgButtonclass}}:{{showImgButtonId}}</b-list-group-item>

    <b-popover :target="`${showImgButtonclass}+${showImgButtonId}`" triggers="hover">
      <template v-slot:title>{{showImgButtonclass}}:{{showImgButtonId}}</template>
      <img
        :src="require(`../../assets/EssentialOilCard/${showImgButtonclass}/${showImgButtonId-1}.png`)"
      />
    </b-popover>
  </div>
</template>

<style lang="scss" scoped>
img {
  width: 100%;
}
</style>

<script>
export default {
  props: {
    showImgButtonId: Number,
    showImgButtonclass: String
  },
  //https://jeremysu0131.github.io/Vue-js-%E7%88%B6%E5%AD%90%E7%B5%84%E4%BB%B6%E6%BA%9D%E9%80%9A-emit-on/
  //https://blog.csdn.net/sllailcp/article/details/78595077
  methods: {
    emitClick() {
      this.$emit("ShowImgButton_Return", this.showImgButtonId - 1);
    }
  }
};
</script>