<template>
  <div>
    <div ref="helloVue" class="EssentialOilCard">
      <img class="bg" :src="require(`../assets/${EssentialOilCard.background}`)" />
      <img class="icon" :src="require(`../assets/${EssentialOilCard.icon}`)" />
      <img class="main" :src="require(`../assets/${EssentialOilCard.main}`)" />
      <!-- <img class="frame" :src="require(`../assets/${EssentialOilCard[3]}`)" />  -->
      <img class="mascot" :src="require(`../assets/${EssentialOilCard.mascot}`)" />
    </div>

    <b-button pill variant="outline-danger" class="camera-button" @click="clickDownloadPage">
      <i class="fas fa-camera"></i>
    </b-button>
    <div v-if="cameraBack" class="camera" @click="cameraBack=false">
      <div v-if="imgUrl" class="page">
        <a :href="imgUrl" download="this is download imgs name">
          <img :src="imgUrl" />
        </a>
      </div>
    </div>
    <!-- <div>{{EssentialOilCard}}</div> -->

    <!-- <div style=" position: fixed;">123：{{this.$route.query.inputEssentialOilCard}}</div> -->

    <!-- ============================== -->
    <div class="control-panel">
      <div>
        <b-tabs content-class="mt-3" align="center">
          <b-tab title="背景" active>
            <p>
              <b-list-group>
                <show-img-button
                  v-for="b in backgroundBtnCount"
                  :key="b"
                  :showImgButtonId="b"
                  showImgButtonclass="background"
                  @ShowImgButton_Return="setBackground"
                />
              </b-list-group>
            </p>
          </b-tab>
          <b-tab title="icon">
            <p>
              <b-list-group>
                <show-img-button
                  v-for="b in backgroundBtnCount"
                  :key="b"
                  :showImgButtonId="b"
                  showImgButtonclass="icon"
                  @ShowImgButton_Return="setIcon"
                />
              </b-list-group>
            </p>
          </b-tab>
          <b-tab title="框">
            <p>
              <!-- <b-list-group>
                <show-img-button
                  v-for="b in backgroundBtnCount"
                  :key="b"
                  :showImgButtonId="b"
                  showImgButtonclass="frame"
                  @ShowImgButton_Return="setFrame"
                />
              </b-list-group> -->
            </p>
          </b-tab>
          <b-tab title="浮水印">
            <p>
              <b-list-group>
                <show-img-button
                  v-for="b in backgroundBtnCount"
                  :key="b"
                  :showImgButtonId="b"
                  showImgButtonclass="mascot"
                  @ShowImgButton_Return="setMascot"
                />
              </b-list-group>
            </p>
          </b-tab>
        </b-tabs>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.control-panel {
  position: absolute;
  top: 5%;
  left: 10%;
}
.camera {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #000000, $alpha: 0.7);
  z-index: 1000;
  text-align: center;
  .page {
    position: absolute;
    background-color: white;
    padding: 3%;
    left: 20%;
    top: 5%;
    width: 60%;
    img {
      width: 100%;
    }
  }
}
.camera-button {
  position: fixed;
  z-index: 999;
  width: 5%;
  height: 10%;
  bottom: 15%;
  right: 10%;
  font-size: 3rem;
}

.EssentialOilCard {
  //pointer-events: none;
  position: absolute;
  right: 5%;
  top: 5%;
  width: 60%;

  .bg {
    width: 100%;
    height: 100%;
    left: 0%;
    top: 0%;
    z-index: 1;
  }
  .main {
    position: absolute;
    height: 70%;
    left: 70%;
    top: 20%;
    z-index: 2;
  }
  .mascot {
    position: absolute;
    height: 50%;
    left: 10%;
    top: 0%;
    z-index: 3;
  }
  .frame {
    position: absolute;
    height: 40%;
    left: 10%;
    top: 20%;
    z-index: 4;
  }
  .icon {
    position: absolute;
    height: 40%;
    left: 30%;
    top: 50%;
    z-index: 5;
  }
}
</style>
<script>
import Vue from "vue";
import VueHtml2Canvas from "vue-html2canvas";
import ShowImgButton from "@/components/input/ShowImgButton.vue";

Vue.use(VueHtml2Canvas);
export default {
  data() {
    return {
      backgroundBtnCount: 3,

      background: "background",
      icon: "icon",
      main: "main",
      frame: "frame",
      mascot: "mascot",
      //===============================
      cameraBack: false,
      imgUrl: null,
      EssentialOilCard: {
        background: null,
        icon: null,
        main: null,
        frame: null,
        mascot: null
      },
      URL: null
    };
  },
  // props: ['inputEssentialOilCard'],
  created() {
    this.inputImgSrcReset();
  },
  methods: {
    setBackground(ButtonId) {
      this.background = ButtonId;
      this.EssentialOilCard.background = this.imgSrc(
        "background",
        this.background
      );
    },
    setIcon(ButtonId) {
      this.icon = ButtonId;
      this.EssentialOilCard.icon = this.imgSrc("icon", this.icon);
    },
    setFrame(ButtonId) {
      this.Frame = ButtonId;
      this.EssentialOilCard.Frame = this.imgSrc("Frame", this.Frame);
    },
    setMascot(ButtonId) {
      this.mascot = ButtonId;
      this.EssentialOilCard.mascot = this.imgSrc("mascot", this.mascot);
    },

    //==============================
    imgSrc(imgFloder, imgNumber) {
      return "EssentialOilCard/" + imgFloder + "/" + imgNumber + ".png";
    },
    inputImgSrcReset() {
      this.EssentialOilCard.background = this.imgSrc("background", 0);
      this.EssentialOilCard.icon = this.imgSrc("icon", 0);
      this.EssentialOilCard.main = this.imgSrc(
        "main",
        this.$route.query.inputEssentialOilCard
      );
      this.EssentialOilCard.frame = this.imgSrc("frame", 0);
      this.EssentialOilCard.mascot = this.imgSrc("mascot", 0);
    },
    /*this is page to img  */
    /*https://www.npmjs.com/package/vue-html2canvas*/

    /*解析度與更多支援 */
    /*https://www.jianshu.com/p/f400634646d6*/
    async clickDownloadPage() {
      this.imgUrl = await this.$html2canvas(this.$refs.helloVue, {
        allowTaint: true,
        taintTest: false,
        type: "dataURL",

        width: "auto",
        height: "auto",
        background: "#fff",
        /*=====關於解析=====*/
        scale: 2,
        dpi: window.devicePixelRatio
      });
      this.cameraBack = true;
    }
    /*
     *Chan丶9樓 2019.11.14 11:53 模糊问题亲测解决：
     *https://github.com/niklasvh/html2canvas/pull/1087
     *关键代码： dpi: 192,scale: 2,
     */
  },
  components: {
    "show-img-button": ShowImgButton
  }
};
</script>


